.mainBlog {
  padding: 120px 0;
  background: #F6F8FD;
}
.mainBlog__list {
  margin: -25px -5px;
  font-size: 0px;
}
.mainBlog__list .item {
  display: inline-block;
  vertical-align: top;
  margin: 25px 5px;
  width: 275px;
}
.mainBlog__list .thumb {
  display: block;
  margin-bottom: 19px;
  height: 203px;
  overflow: hidden;
  transition: 0.3s;
}
.mainBlog__list .thumb img {
  width: 100%;
  height: auto;
}
.mainBlog__list .tit {
  position: relative;
  display: block;
  margin-bottom: 12px;
  font-size: 28px;
  line-height: 1em;
  color: #000;
}
.mainBlog__list .tit::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 25px;
  height: 25px;
  background: #F9C94E url(../img/ico__more.svg) no-repeat 50% 50%;
  border-radius: 5px;
}
.mainBlog__list .txt {
  display: -webkit-box;
  font-size: 18px;
  line-height: 140%;
  color: #000;
  opacity: 0.5;
  word-break: normal;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}