/* mainStep */
.mainStep {
  padding: 120px 0;
  background-color: #F6F8FD;
}
.mainStep .in {
  position: relative;
  padding-left: 413px;
  box-sizing: border-box;
}
.mainStep__header {
  position: absolute;
  left: 20px;
}
.mainStep__header .tit {
  margin-bottom: 26px;
  font-size: 50px;
  line-height: 120%;
  color: #000;
}
.mainStep__header .titBullet {
  display: block;
  margin-bottom: 10px;
  padding: 3px 0;
  width: 150px;
  text-align: center;
  font-size: 14px;
  line-height: 140%;
  color: #ffffff;
  background: #FF3344;
  border-radius: 30px;
}
.mainStep__header .txt {
  font-size: 18px;
  line-height: 150%;
  color: #000;
}
.mainStep__list {
  position: relative;
  top: -26px;
  font-size: 0px;
}
.mainStep__list .item {
  display: inline-block;
  vertical-align: top;
  margin: 26px 10px;
  width: 358px;
}
.mainStep__list .tit {
  display: block;
  margin-bottom: 20px;
  font-size: 24px;
  line-height: 1em;
  color: #000;
}
.mainStep__list .tit:before {
  content: "";
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-bottom: 10px;
  background: #ddd;
}
.mainStep__list .txt {
  font-size: 18px;
  line-height: 150%;
  color: #000;
}