/* main visual */
.visual__header {
  position: absolute;
}
.visual__tit {
  margin: 90px 0 23px 0;
  font-size: 110px;
  color: #000;
}
.visual__txt {
  font-size: 24px;
  line-height: 1.5em;
  color: #0C0C0C;
}
.visual__view {
  margin-top: 29px;
  display: inline-block;
  font-size: 18px;
  line-height: 1em;
  color: #ffffff;
  background: #4857EC;
}
.visual__view a {
  display: block;
  padding: 15px 67px;
}
.visual__slide {
  text-align: center;
  font-size: 0px;
}